@media print {
    * {
        color-adjust: exact;
        -webkit-print-color-adjust: exact;
    }

    @page {
        padding: 0 !important;
    }

    #header {
        display: none !important;
    }

    #base-content {
        padding: 0 !important;
        background-color: transparent !important;
    }

    #settings-toolbar {
        display: none !important;
    }

    #my-cv {
        box-shadow: none !important;
    }

    #save-to-pdf-btn {
        display: none !important;
    }

    #footer {
        display: none !important;
    }
}

body {
    margin: 0;
    padding: 0;
    font-family: 'Roboto', 'Segoe UI', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    word-break: break-word;
}

div.rangeslider .rangeslider__handle {
    width: 20px;
    height: 20px;
    outline: none;
}

div.rangeslider-horizontal {
    height: 6px;
    background-color: #666;
}

div.rangeslider-horizontal .rangeslider__fill {
    background-color: #ddd;
}

div.rangeslider .rangeslider__handle::after {
    display: none;
}

a {
    text-decoration: none;
}
